<template>
    <div class="ap-container" @click="goTo">
        <div class="article-preview">
            <div class="ap-img">
                <Skeleton class="custom-skeleton" v-if="isLoading || !imageUrl" />
                <img v-else :src="imageUrl" alt="Article Image" />
            </div>
            <div class="ap-desc-text">
                <div class="ap-date">
                    <Skeleton class="skeleton-text">
                        {{ date }}
                    </Skeleton>
                </div>
                <div class="ap-title">
                    <Skeleton>
                        {{ apPreviewTitle }}
                    </Skeleton>
                </div>
                <div class="ap-description">
                    <Skeleton>
                        {{ apPreviewDescription }}
                    </Skeleton>
                </div>
                <div class="ap-footer">
                    <div class="ap-tags">
                        <div class="tag" v-for="tag in tags" :key="tag">
                            {{ tag }}
                        </div>
                    </div>
                    <span class="ap-read">
                        Read More >
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { Skeleton } from 'vue-loading-skeleton';
import "vue-loading-skeleton/dist/style.css"

export default {
    components: {
        Skeleton
    },
    props: {
        date: String, // Предположим, что date - это строка, замените на соответствующий тип данных, если необходимо
        apPreviewTitle: String,
        apPreviewDescription: String,
        tags: Array, // Предположим, что tags - это массив строк, замените на соответствующий тип данных, если необходимо
        imageUrl: String,
        artID: String
    },
    methods: {
        goTo() {
            this.$router.push({ path: '/article', query: { id: this.artID } })

            console.log(this.artID)
        }
    },
    data() {
        return {
            isLoading: true, // Изначально считаем, что данные загружаются
        };
    },
    mounted() {
        // Симулируем загрузку данных, например с помощью setTimeout
        setTimeout(() => {
            this.isLoading = false; // Когда данные загружены, устанавливаем isLoading в false
        }, 200); // Эмулируем задержку загрузки в 2 секунды (вы можете изменить это значение)
    },

}
</script>
