                    <p class="error-message " v-if="emailError || (submitted && email === '')">{{ emailError }}</p>
                </div>
                <div class="suc-subscribe" v-if="submitForm">
                    <div class="sc-sbu-img">
                        <svg width="86" height="86" viewBox="0 0 86 86" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M21.6667 43.0001L35.8889 57.2223L64.3333 28.7778" stroke="#007F82" stroke-width="1.5"
                                stroke-linecap="round" stroke-linejoin="round" />
                        </svg>
                    </div>
                    <div class="sc-sub-text">You have successfully subscribed</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import getObjectFromRoot from "../getData";
import he from "he"; // Импортируем библиотеку he
import $ from "jquery";


export default {
    
    data() {
        return {
            rootData: [],
            currentData: [],
            parsedContent: null,
            showreview: false,
            email: "",
            emailError: "",
            submitted: false, // Добавляем переменную для отслеживания отправки формы


        }
    },
    async created() {
        this.rootData = await getObjectFromRoot();
        console.log(this.rootData)

        const index = this.$route.query.id;
        this.currentData = this.rootData[index]
        if (index === 5) {
            this.showreview = true
        } else {
            this.showreview = false
        }
        console.log(this.currentData)
        this.parseContent();
    },

    methods: {
        validateEmail() {
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailPattern.test(this.email)) {
                this.emailError = "Please enter a valid email";
            } else {
                this.emailError = "";
            }
        },
        sendData() {
            this.submitted = true; // Устанавливаем флаг отправки формы
            if (!this.emailError && this.email !== "") {
                this.submitForm = true
                $.ajax({
                    method: 'POST',
                    url: 'https://yourufx.space/subscribe/submit-form',
                    data: { email: this.email },
                })
                    .done(function () {
                        this.submitForm = true
                    })
                    .fail(function () {
                        this.submitForm = true

                    })
            }
        },
        parseContent() {
            if (this.currentData.content) {
                // Декодируем HTML-сущности
                const decodedContent = he.decode(this.currentData.content);

                // Заменяем HTML-сущности для изображения на корректный тег img
                const imgTagReplacedContent = decodedContent.replace(
                    /&lt;img\s+src=&quot;([^&]*)&quot;\s+alt=&quot;([^&]*)&quot;\s+\/&gt;/g,
                    '<img style="display: flex; justify-content: center; width: 100%; height: 100%" src="$1" alt="$2" />'
                );

                this.parsedContent = imgTagReplacedContent;
            }
        },
    }
}
</script>
