                    <input type="email" class="sub-input"
                        :class="{ 'invalid-input': emailError || (submitted && email === '') }" v-model="email"
                        @input="validateEmail" placeholder="Add your email" />
                    <button class="subscribe-btn" @click="sendData">Subscribe</button>
                    <p class="error-message error-sub" v-if="emailError || (submitted && email === '')">{{ emailError }}</p>
                </div>
