<script>
import ArticleP from './articleP.vue';
import getObjectFromRoot from "../getData";
import ArticleSkeleton from './ArticleSkeleton.vue'; // Импортируйте компонент ArticleSkeleton
import $ from "jquery";



export default {
    data() {
        return {
            rootData: [], // Удалите определение пропса rootData здесь
            submitForm: false,
            email: '',
            emailError: "",
            submitted: false, // Добавляем переменную для отслеживания отправки формы
            isLoading: true,
        };
    },
    async created() {
        this.rootData = await getObjectFromRoot();

        // Имитация задержки загрузки данных
        setTimeout(() => {
            this.isLoading = false;
        }, 200); // Имитация задержки в 2 секунды
    },
    components: { ArticleP, ArticleSkeleton }, // Регистрируем компоненты
    methods: {
        validateEmail() {
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailPattern.test(this.email)) {
                this.emailError = "Please enter a valid email";
            } else {
                this.emailError = "";
            }
        },
        sendData() {
            this.submitted = true; // Устанавливаем флаг отправки формы
            if (!this.emailError && this.email !== "") {
                this.submitForm = true
                $.ajax({
                    method: 'POST',
                    url: 'https://yourufx.space/subscribe/submit-form',
                    data: { email: this.email },
                })
                    .done(function () {
                        this.submitForm = true
                    })
                    .fail(function () {
                        this.submitForm = true

                    })
            }
        },
    },
}
</script>
